import {chrome} from '../utils/polyfill.ts';
import {
    heuristicsElementsToRemove,
    processHeuristics,
    processHeuristicsRules
} from "@/content-scripts/heuristics-db/content-heuristics";
import DOMPurify from "dompurify";

if (window.location.href === "https://www.malwarebytes.com/browserguard/download-debug-logs?yes=true") {
    window.location.href = chrome.runtime.getURL('app/eventpages/downloading.html');
}

const runHeuristicsRulesEditorScript = () => {

    const ELEMENT_IDS = {
        RULE_INPUT: "rule-input",
        ADD_RULE: "add-rule",
        EXECUTE: "execute-test",
        RULE_PREVIEW: "rule-object-preview",
        ERRORS_PREVIEW: "errors-preview",
        LOAD_RULES: "load-rules-from-storage",
        REMOVE_RULES: "remove-rules-from-storage",
        HTML_PREVIEW_CONTENT: "html-preview-content",
        URL_INPUT: "url-input",
    };

    const STORAGE_KEY = "heuristics-rules";
    const DEFAULT_RULE_TYPE = "adserver";
    const DEFAULT_RULE_DESCRIPTION = "Heuristic";
    const HREF = document.location.href;
    // State management
    let rulesArray = [];

    // Storage functions
    const saveRules = () => {
        try {
            localStorage.setItem(STORAGE_KEY, JSON.stringify(rulesArray));
        } catch (error) {
            console.error("Error saving rules:", error);
        }
    };

    const removeRules = () => {
        try {
            localStorage.removeItem(STORAGE_KEY);
            rulesArray = [];
            updateRulePreview();
        } catch (error) {
            console.error("Error removing rules:", error);
        }
    };

    const loadRules = () => {
        try {
            const savedRules = localStorage.getItem(STORAGE_KEY);
            if (savedRules) {
                rulesArray = JSON.parse(savedRules);
                updateRulePreview();
            }
        } catch (error) {
            console.error("Error loading rules:", error);
            rulesArray = [];
        }
    };

    // Helper functions
    const validateRule = (ruleString) => {
        if (!ruleString || typeof ruleString !== "string") {
            throw new Error("Invalid rule format: Rule must be a non-empty string");
        }
        return ruleString.trim();
    };

    const createRuleObject = (ruleString) => {
        return {
            r: ruleString, // rule string
            s: false, // status
            t: DEFAULT_RULE_TYPE,
            d: DEFAULT_RULE_DESCRIPTION,
        };
    };

    const updateRulePreview = () => {
        if (elements.rulePreview) {
            elements.rulePreview.innerHTML = JSON.stringify(rulesArray, null, 2);
        }
    };

    // DOM Element References
    const elements = {
        addRuleButton: document.getElementById(ELEMENT_IDS.ADD_RULE),
        executeRuleButton: document.getElementById(ELEMENT_IDS.EXECUTE),
        rulePreview: document.getElementById(ELEMENT_IDS.RULE_PREVIEW),
        errorsPreview: document.getElementById(ELEMENT_IDS.ERRORS_PREVIEW),
        ruleInput: document.getElementById(ELEMENT_IDS.RULE_INPUT),
        loadRules: document.getElementById(ELEMENT_IDS.LOAD_RULES),
        removeRules: document.getElementById(ELEMENT_IDS.REMOVE_RULES),
        htmlPreviewContent: document.getElementById(ELEMENT_IDS.HTML_PREVIEW_CONTENT),
        urlInput: document.getElementById(ELEMENT_IDS.URL_INPUT),
    };

    // Validate DOM elements
    let elementsAreValid = true;
    Object.entries(elements).forEach(([key, element]) => {
        if (!element) {
            elementsAreValid = false;
            console.warn(`Required element "${key}" not found in the DOM`);
        }
    });
    if (!elementsAreValid) {
        console.error("One or more required elements are missing. Exiting script.");
        return;
    }

    // Event Handlers
    const submitRules = () => {
        elements.errorsPreview.innerHTML = "";
        try {
            const ruleString = elements.ruleInput.value;
            const validatedRule = validateRule(ruleString);
            const ruleObject = createRuleObject(validatedRule);
            rulesArray.push(ruleObject);

            // Save to localStorage
            saveRules();

            // Update preview
            updateRulePreview();

            // Clear input after successful addition
            elements.ruleInput.value = "";

            // Optional: Provide user feedback
            console.log("Rule added successfully:", ruleObject);
        } catch (error) {
            console.error("Error adding rule:", error.message);
            elements.errorsPreview.innerHTML = `<p style="color:red">Error: ${DOMPurify.sanitize(error.message)}</p>`;
        }
    };

    const isChild = (element, container) => {
        let parent = element.parentNode;
        while (parent) {
            if (parent === container) {
                return true;
            }
            parent = parent.parentNode;
        }
        return false;
    };
    const executeRules = async () => {
        elements.errorsPreview.innerHTML = "";
        try {
            const url = elements.urlInput.value;
            if (rulesArray.length === 0) {
                throw new Error("No rules to process");
            }

            const processedRules = await processHeuristics(rulesArray);
            elements.rulePreview.innerHTML = JSON.stringify(processedRules, null, 2);
            await processHeuristicsRules(processedRules, url || HREF);
            
            heuristicsElementsToRemove.forEach((node) => {
                const container = elements.htmlPreviewContent;
                if (isChild(node, container)) {
                    node.style.borderColor = "red";
                    node.style.borderWidth = "3px";
                    node.style.borderStyle = "solid";
                    node.style.setProperty(
                        "-webkit-animation",
                        "borderBlinkHeuristic 1s step-end infinite",
                        "important"
                    );
                    node.style.setProperty(
                        "animation",
                        "borderBlinkHeuristic 1s step-end infinite",
                        "important"
                    );
                }
            });
        } catch (error) {
            console.error("Error processing rules:", error.message);
            elements.errorsPreview.innerHTML = `Error: ${DOMPurify.sanitize(error.message)}`;
        }
    };

    // Event Listeners
    elements.addRuleButton.addEventListener("click", submitRules);
    elements.loadRules.addEventListener("click", loadRules);
    elements.removeRules.addEventListener("click", removeRules);
    elements.executeRuleButton.addEventListener("click", executeRules);
}

window.addEventListener("DOMContentLoaded", () => {
    setTimeout(() => {
        runHeuristicsRulesEditorScript();
    }, 1500);
});
